.department-selection {
  display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
  }
  
  .departments {
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 25%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap; 
    margin-bottom: 10px;
    margin-top: 30px;
    margin-bottom: 1rem;
    border-radius: 50px;
  }
  
  .department {
    display: block;
    text-decoration: none;
    color: #fff;
    background-color:#333 ;
    padding: 10px;
    border-radius: 5px;
    text-align: center;
    font-size: 16px;
    margin-bottom: 20px;
  }
  
  .department:hover {
    background-color: #ccc;
    color: #333;
  }
  .department svg {
    margin-right: 10px; 
    font-size: 1.2rem; 
    color: white; 
  }
  